<template><section>

<h2 id="collapse-zhe-die-mian-ban"><a class="header-anchor" href="#collapse-zhe-die-mian-ban" aria-hidden="true">¶</a> Collapse 折叠面板</h2>
<p>通过折叠面板收纳内容区域。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<p>同时只能展开一个面板。具体见个人中心-个人设置页面。</p>
<docs-demo-block> 
                                                <div class="source" slot="source">  <fm-collapse>
      <fm-collapse-item title="头像">
          <div class="collapse-content">个人设置-上传头像</div>
      </fm-collapse-item>
      <fm-collapse-item title="基本资料">
          <div class="collapse-content">个人设置-基本资料</div>
      </fm-collapse-item>
  </fm-collapse>
</div>
                                                <p>同时只能展开一个面板。</p>
<pre v-pre=""><code class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"头像"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>个人设置-上传头像<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"基本资料"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>个人设置-基本资料<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse</span>&gt;</span>
</code></pre>
</docs-demo-block>
<p>也可以按照个人中心-交易设置页面处理：</p>
<docs-demo-block> 
                                                <div class="source" slot="source">  <fm-collapse>
      <fm-collapse-item title="交易账号" act-text="编辑 收起">
          <div class="collapse-content">
              <p>交易账号</p>
              <p>展开/收缩文本需要以空格分开</p>
          </div>
      </fm-collapse-item>
      <fm-collapse-item title="快速下单" info="已开启" act-text="编辑 收起">
          <div class="collapse-content">
              <p>快速下单</p>
              <p>info 用于设置简要说明</p>
          </div>
      </fm-collapse-item>
      <fm-collapse-item title="快速平仓" info="带 icon" act-text="编辑 收起" icon-classes="edit-icon edit-hover-icon edit-icon-active edit-hover-icon-active">
          <div class="collapse-content">
              <p>快速平仓</p>
              <p>设置右侧 icon：icon 的类以空格分开</p>
              <p>类名包含 hover 的是鼠标 hover 上去添加的类，包含 active 的是面板展开时添加的类，带 hover &amp; active 的是展开时鼠标 hover 时添加的类</p>
          </div>
      </fm-collapse-item>
  </fm-collapse>
</div>
                                                <p>可以设置简要说明、右侧区域的展开/收缩文本。</p>
<pre v-pre=""><code class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"交易账号"</span> <span class="hljs-attr">act-text</span>=<span class="hljs-string">"编辑 收起"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>交易账号<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>展开/收缩文本需要以空格分开<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"快速下单"</span> <span class="hljs-attr">info</span>=<span class="hljs-string">"已开启"</span> <span class="hljs-attr">act-text</span>=<span class="hljs-string">"编辑 收起"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>快速下单<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>info 用于设置简要说明<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"快速平仓"</span> <span class="hljs-attr">info</span>=<span class="hljs-string">"带 icon"</span> <span class="hljs-attr">act-text</span>=<span class="hljs-string">"编辑 收起"</span> <span class="hljs-attr">icon-classes</span>=<span class="hljs-string">"edit-icon edit-hover-icon edit-icon-active edit-hover-icon-active"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>快速平仓<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>设置右侧 icon：icon 的类以空格分开<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>类名包含 hover 的是鼠标 hover 上去添加的类，包含 active 的是面板展开时添加的类，带 hover &amp; active 的是展开时鼠标 hover 时添加的类<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="shou-feng-qin-xiao-guo"><a class="header-anchor" href="#shou-feng-qin-xiao-guo" aria-hidden="true">¶</a> 手风琴效果</h3>
<p>展开多个面板。</p>
<docs-demo-block> 
                                                <div class="source" slot="source">  <fm-collapse accordion="">
      <fm-collapse-item title="头像">
          <div class="collapse-content">个人设置-上传头像</div>
      </fm-collapse-item>
      <fm-collapse-item title="基本资料">
          <div class="collapse-content">个人设置-基本资料</div>
      </fm-collapse-item>
  </fm-collapse>
</div>
                                                <p>设置 <code>accordion</code> 为 true，可实现手风琴效果。</p>
<pre v-pre=""><code class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse</span> <span class="hljs-attr">accordion</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"头像"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>个人设置-上传头像<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"基本资料"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>个人设置-基本资料<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="mo-ren-zhan-kai-mian-ban"><a class="header-anchor" href="#mo-ren-zhan-kai-mian-ban" aria-hidden="true">¶</a> 默认展开面板</h3>
<docs-demo-block> 
                                                <div class="source" slot="source">  <fm-collapse>
      <fm-collapse-item title="默认展开" ref="list">
          <div class="collapse-content">默认展开的面板</div>
      </fm-collapse-item>
  </fm-collapse>
</div>
                                                <p>方法调用: <code>this.$refs.list.showCollapseItem()</code>。</p>
<pre v-pre=""><code class="hljs language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">fm-collapse-item</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"默认展开"</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">'list'</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"collapse-content"</span>&gt;</span>默认展开的面板<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">fm-collapse</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="collapse-shu-xing"><a class="header-anchor" href="#collapse-shu-xing" aria-hidden="true">¶</a> Collapse 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">accordion</td>
<td style="text-align:left">是否启用手风琴效果</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left">false</td>
</tr>
</tbody>
</table>
<h3 id="collapse-item-shu-xing"><a class="header-anchor" href="#collapse-item-shu-xing" aria-hidden="true">¶</a> Collapse Item 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">title</td>
<td style="text-align:left">面板标题</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">info</td>
<td style="text-align:left">标题辅助说明</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">act-text</td>
<td style="text-align:left">空格分开的文本</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
<tr>
<td style="text-align:left">icon-classes</td>
<td style="text-align:left">icon 的类名，空格分开</td>
<td style="text-align:left">string</td>
<td style="text-align:left">-</td>
<td style="text-align:left">''</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less">
    .collapse-content {
        padding: 20px;
        height: 150px;
    }
    .fm-collapse-item {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0px;
        }
    }
    .edit-icon{
        display: inline-block;
        width: 9px;
        height: 10px;
        background: url("//alibetawww.followme.com/portalindex/assets/account-nav-icon.png") no-repeat -120px 0;
        vertical-align: top;
        margin: 2px 0 0 0;
        &:hover{
            background-position: -160px 0;
        }
        &.edit-hover-icon{
            background-position: -160px 0;
        }
        &.edit-icon-active{
            background-position: -120px -20px;
            &:hover{
                background-position: -160px -20px;
            }
            &.edit-hover-icon-active{
                background-position: -160px -20px;
            }
        }
    }
</style>
<script>
    export default {
        mounted () {
            if (this.$refs.list) {
                this.$refs.list.showCollapseItem();
            }
        }
    }
</script>


